'use client';

import { useState, useEffect } from "react";
import toast from "react-hot-toast";
import { useRouter } from "next/navigation";
import { useSession } from "next-auth/react";
import { onboardingAPI, schoolAPI, programAPI, userAPI } from "@/lib/api";
import { LoaderIcon, GraduationCapIcon, BuildingIcon } from "lucide-react";

// Define the error type for form errors
interface FormErrors {
    schoolId?: string;
    programId?: string;
}

// Define types for school and program data
interface School {
    _id: string;
    name: string;
    code: string;
    type: string;
    province: string;
}

interface Program {
    _id: string;
    name: string;
    code: string;
    level: string;
    duration: {
        semesters: number;
        years: number;
    };
    totalCredits: number;
}

interface Province {
    name: string;
    schools: School[];
}

export default function OnboardingPage() {
    const router = useRouter();
    const { data: session, update } = useSession();
    const user = session?.user;
    
    const [isLoading, setIsLoading] = useState(false);
    const [errors, setErrors] = useState<FormErrors>({});
    
    // Form data
    const [selectedProvince, setSelectedProvince] = useState<string>('');
    // Remove these as we're using formData now
    
    // Data states
    const [provinces, setProvinces] = useState<string[]>([]);
    const [schools, setSchools] = useState<School[]>([]);
    const [programs, setPrograms] = useState<Program[]>([]);
    
    // Loading states
    const [loadingProvinces, setLoadingProvinces] = useState(false);
    const [loadingSchools, setLoadingSchools] = useState(false);
    const [loadingPrograms, setLoadingPrograms] = useState(false);

    // Th√™m state cho search tr∆∞·ªùng
    const [schoolSearch, setSchoolSearch] = useState('');
    // Filter tr∆∞·ªùng theo search
    const filteredSchools = schools.filter(school =>
        school.name.toLowerCase().includes(schoolSearch.toLowerCase())
    );

    const [isEditMode, setIsEditMode] = useState(false);

    const [formData, setFormData] = useState({
        province: '',
        schoolId: '',
        programId: ''
    });

    // Load provinces on component mount
    useEffect(() => {
        // Check if in edit mode
        const urlParams = new URLSearchParams(window.location.search);
        const editMode = urlParams.get('edit') === 'true';
        setIsEditMode(editMode);

        loadProvinces();
        
        // Load existing data if in edit mode
        if (editMode) {
            loadExistingData();
        }
    }, []);

    // Load schools when province changes
    useEffect(() => {
        if (selectedProvince) {
            loadSchoolsByProvince(selectedProvince);
            // Reset selections when province changes
                    setFormData(prev => ({ ...prev, schoolId: '', programId: '' }));
            setPrograms([]);
        }
    }, [selectedProvince]);

    // Load programs when school changes
    useEffect(() => {
        if (formData.schoolId) {
            loadProgramsBySchool(formData.schoolId);
            // Reset program selection when school changes
            setFormData(prev => ({ ...prev, programId: '' }));
        }
    }, [formData.schoolId]);

    const loadProvinces = async () => {
        try {
            setLoadingProvinces(true);
            const response = await schoolAPI.getProvinces();
            
            if (response.success && response.data?.provinces) {
                setProvinces(response.data.provinces);
            }
        } catch (error) {
            console.error('Error loading provinces:', error);
            toast.error('Failed to load provinces');
        } finally {
            setLoadingProvinces(false);
        }
    };

    const loadSchoolsByProvince = async (province: string) => {
        try {
            setLoadingSchools(true);
            const response = await schoolAPI.getSchoolsByProvince(province);
            
            if (response.success && response.data?.schools) {
                setSchools(response.data.schools);
            }
        } catch (error) {
            console.error('Error loading schools:', error);
            toast.error('Failed to load schools');
        } finally {
            setLoadingSchools(false);
        }
    };

    const loadProgramsBySchool = async (schoolId: string) => {
        try {
            setLoadingPrograms(true);
            const response = await programAPI.getProgramsBySchool(schoolId);
            
            if (response.success && response.data?.programs) {
                setPrograms(response.data.programs);
            }
        } catch (error) {
            console.error('Error loading programs:', error);
            toast.error('Failed to load programs');
        } finally {
            setLoadingPrograms(false);
        }
    };

    const loadExistingData = async () => {
        try {
            console.log('üìã Loading existing user data for edit...');
            const response = await userAPI.getProfile();
            if (response.success && response.data.academic) {
                const academic = response.data.academic;
                console.log('üìã Existing academic data:', academic);
                
                if (academic.school && academic.program) {
                    // Set form data
                    setFormData({
                        province: academic.school.province || '',
                        schoolId: academic.school._id || '',
                        programId: academic.program._id || ''
                    });
                    
                    // Set selected province for UI
                    if (academic.school.province) {
                        setSelectedProvince(academic.school.province);
                        // Load schools for this province
                        await loadSchoolsByProvince(academic.school.province);
                        // Load programs for this school
                        if (academic.school._id) {
                            await loadProgramsBySchool(academic.school._id);
                        }
                    }
                }
            }
        } catch (error) {
            console.error('‚ùå Error loading existing data:', error);
        }
    };

    const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        setIsLoading(true);
        setErrors({});

        // Client-side validation
        const newErrors: FormErrors = {};
        if (!formData.schoolId) newErrors.schoolId = 'Please select a school';
        if (!formData.programId) newErrors.programId = 'Please select a program';

        if (Object.keys(newErrors).length > 0) {
            setErrors(newErrors);
            setIsLoading(false);
            return;
        }

        try {
            const result = await onboardingAPI.complete({
                            schoolId: formData.schoolId,
            programId: formData.programId,
                currentSemester: 1, 
                enrollmentYear: new Date().getFullYear() // Current year
            });

            if (result.success && result.data?.user) {
                // Update NextAuth session with new user data
                await update({
                    ...session,
                    user: {
                        ...session?.user,
                        ...result.data.user,
                        isOnboarded: true
                    }
                });
                
                toast.success("Onboarding completed successfully!");
                
                setTimeout(() => {
                    router.push('/dashboard');
                }, 1000);
            } else {
                toast.error(result.message || "Failed to complete onboarding");
            }
        } catch (error) {
            console.error('Onboarding error:', error);
            toast.error("An unexpected error occurred");
        } finally {
            setIsLoading(false);
        }
    };

    const handleSkip = async () => {
        try {
            setIsLoading(true);
            console.log('üö´ Skipping onboarding...');
            
            const response = await onboardingAPI.skip();
            
            if (response.success) {
                toast.success('Onboarding skipped! You can complete it later.');
                console.log('‚úÖ Onboarding skipped successfully');
                
                setTimeout(() => {
                    router.push('/');
                }, 1000);
            } else {
                toast.error(response.message || 'Failed to skip onboarding');
            }
        } catch (error: any) {
            console.error('‚ùå Error skipping onboarding:', error);
            toast.error(error?.response?.data?.message || 'Failed to skip onboarding');
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="min-h-screen bg-[#F9F6EE] flex items-center justify-center p-4">
            <div className="card bg-base-200 w-full max-w-2xl shadow-xl">
                <div className="card-body p-6 sm:p-8">
                    <div className="text-center mb-8">
                        <GraduationCapIcon className="w-12 h-12 mx-auto mb-4 text-primary" />
                        <h1 className="text-3xl font-bold text-base-content mb-2">
                            {isEditMode ? 'Update Academic Information' : 'Complete Your Academic Profile'}
                        </h1>
                        <p className="text-base-content opacity-70">
                            {isEditMode 
                                ? 'Update your school and program information'
                                : 'Help us personalize your learning experience by providing your academic details'
                            }
                        </p>
                    </div>

                    <form onSubmit={handleSubmit} className="space-y-6">
                        {/* PROVINCE SELECTION */}
                        <fieldset className="fieldset">
                            <label className="label" htmlFor="province">
                                <span>Province *</span>
                            </label>
                            <select
                                id="province"
                                value={formData.province}
                                onChange={(e) => {
                                    const province = e.target.value;
                                    setFormData(prev => ({ ...prev, province, schoolId: '', programId: '' }));
                                    setSelectedProvince(province);
                                }}
                                className="select w-full"
                                disabled={isLoading}
                            >
                                <option value="" disabled>
                                    {loadingProvinces ? 'Loading provinces...' : 'Select your province'}
                                </option>
                                {provinces.map((province) => (
                                    <option key={province} value={province}>
                                        {province}
                                    </option>
                                ))}
                            </select>
                        </fieldset>

                        {/* SCHOOL SELECTION */}
                        <fieldset className="fieldset">
                            <label className="label" htmlFor="school">
                                <span>School *</span>
                            </label>
                            {/* Input search tr∆∞·ªùng */}
                            <input
                                type="text"
                                placeholder="Search school..."
                                value={schoolSearch}
                                onChange={e => setSchoolSearch(e.target.value)}
                                className="input w-full mb-2"
                                disabled={!selectedProvince || loadingSchools || isLoading}
                            />
                            <select
                                id="school"
                                value={formData.schoolId}
                                onChange={(e) => setFormData(prev => ({ ...prev, schoolId: e.target.value }))}
                                className="select w-full"
                                disabled={!selectedProvince || loadingSchools || isLoading}
                            >
                                <option value="" disabled>
                                    {!selectedProvince 
                                        ? 'Please select a province first' 
                                        : loadingSchools 
                                        ? 'Loading schools...' 
                                        : 'Select your school'
                                    }
                                </option>
                                {filteredSchools.map((school) => (
                                    <option key={school._id} value={school._id}>
                                        {school.name} ({school.type})
                                    </option>
                                ))}
                            </select>
                            {errors.schoolId && (
                                <label className="label">
                                    <span className="text-error text-sm">{errors.schoolId}</span>
                                </label>
                            )}
                        </fieldset>

                        {/* PROGRAM SELECTION */}
                        <fieldset className="fieldset">
                            <label className="label" htmlFor="program">
                                <span>Program *</span>
                            </label>
                            <select
                                id="program"
                                value={formData.programId}
                                onChange={(e) => setFormData(prev => ({ ...prev, programId: e.target.value }))}
                                className="select w-full"
                                disabled={!formData.schoolId || loadingPrograms || isLoading}
                            >
                                <option value="" disabled>
                                    {!formData.schoolId 
                                        ? 'Please select a school first' 
                                        : loadingPrograms 
                                        ? 'Loading programs...' 
                                        : 'Select your program'
                                    }
                                </option>
                                {programs.map((program) => (
                                    <option key={program._id} value={program._id}>
                                        {program.name} ({program.level}) - {program.duration.years} years
                                    </option>
                                ))}
                            </select>
                            {errors.programId && (
                                <label className="label">
                                    <span className="text-error text-sm">{errors.programId}</span>
                                </label>
                            )}
                        </fieldset>

                        {/* SELECTED PROGRAM INFO */}
                        {formData.programId && programs.length > 0 && (
                            <div className="bg-base-300 p-4 rounded-lg">
                                <h3 className="font-semibold mb-2">Selected Program Details:</h3>
                                {programs.find(p => p._id === formData.programId) && (
                                    <div className="text-sm space-y-1">
                                        <p><strong>Name:</strong> {programs.find(p => p._id === formData.programId)?.name}</p>
                                        <p><strong>Level:</strong> {programs.find(p => p._id === formData.programId)?.level}</p>
                                        <p><strong>Duration:</strong> {programs.find(p => p._id === formData.programId)?.duration.semesters} semesters ({programs.find(p => p._id === formData.programId)?.duration.years} years)</p>
                                        <p><strong>Total Credits:</strong> {programs.find(p => p._id === formData.programId)?.totalCredits}</p>
                                    </div>
                                )}
                            </div>
                        )}

                        {/* Action Buttons */}
                        <div className="flex flex-col sm:flex-row gap-4 mt-8">
                            {isEditMode && (
                                <button
                                    type="button"
                                    onClick={() => router.push('/')}
                                    className="btn btn-ghost flex-1"
                                >
                                    Cancel
                                </button>
                            )}
                            
                            <button
                                type="submit"
                                disabled={isLoading || !formData.province || !formData.schoolId || !formData.programId}
                                className="btn btn-primary flex-1"
                            >
                                {isLoading ? (
                                    <>
                                        <span className="loading loading-spinner loading-sm"></span>
                                        {isEditMode ? 'Updating...' : 'Completing...'}
                                    </>
                                ) : (
                                    isEditMode ? 'Update Information' : 'Complete Onboarding'
                                )}
                            </button>
                            
                            {!isEditMode && (
                                <button
                                    type="button"
                                    onClick={handleSkip}
                                    disabled={isLoading}
                                    className="btn btn-ghost flex-1"
                                >
                                    Skip for now
                                </button>
                            )}
                        </div>
                    </form>
                </div>
            </div>
        </div>
    );
}